import React from 'react';
import './App.css'
import Home from './pages/Home'
import SearchResults from './pages/SearchResults'
import Detail from './pages/Detail'
import StaticContext from './context/StaticContext'
import {GifsContextProvider} from './context/GifsContext'
import { Link, Route } from "wouter"

export default function App() {
  return (
  <StaticContext.Provider value={{name: 'midudev',  suscribeteAlCanal: true}}>
      {/* El contexto envuelve a todo para que todos los elementos y componentes dentro del contexto puedan acceder a √©l */}
      <div className="App">
        <section className="App-content">
          <Link to="/">
            <figure className="App-logo">
              <img alt='Giffy logo' src='/logo.png' />
            </figure>
          </Link>
          {/* Men√∫ original */}{/* As√≠ se comenta dentro del bloque de representaci√≥n donde usamos JSX */}
          <GifsContextProvider>
            <Route
              component={Home}
              path="/"
            />
            <Route
              component={SearchResults}
              path="/search/:keyword"  />
            <Route
              component={Detail}
              path="/gif/:id"
            />
          </GifsContextProvider>
        </section>
      </div>
    </StaticContext.Provider>
  )
}
