import React, {useState} from "react"
import { useLocation } from "wouter" // useLocation es un hook de wouter
import ListOfGifs from 'components/ListOfGifs'
import {useGifs} from 'hooks/useGifs'
import TrendingSearches from 'components/TrendingSearches'

export default function Home() {
  const [keyword, setKeyword] = useState('')
  const [path, pushLocation] = useLocation()

  const {loading, gifs} = useGifs() // Aqu√≠ usamos un custom hook para mostrar algunos por defecto


    // Extraemos los m√©todos de callback en constantes por limpieza de c√≥digo. Dejarlos dentro del JSX es m√°s lioso
  const handleSubmit = evt => {
    evt.preventDefault()  // As√≠ evitamos el comportamiento por defecto de la p√°gina, que ser√≠a refrescarse y borrar el contenido del imputbox
    pushLocation(`/search/${keyword}`)  //usamos el hook de Wouter para navegar program√°ticamente a la ruta de b√∫squedas
  }

  const handleChange = evt => {
    // Cada vez que actualizamos el contenido del input actualizamos el keyword
    setKeyword(evt.target.value)
  }

  return (
    <>
      <form onSubmit={handleSubmit}>
        <button>Buscar</button>
        <input placeholder="Search a gif here..." onChange={handleChange} type='text' value={keyword} />
      </form>
      <div className="App-main">
        <div className="App-results">
          <h3 className="App-title">√öltima b√∫squeda</h3>
          <ListOfGifs gifs={gifs} />
        </div>
        <div className="App-category">
          <TrendingSearches />
        </div>
      </div>
    </>
  )
}